@import url(https://fonts.googleapis.com/css2?family=Teko:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Andika+New+Basic&display=swap);
* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  z-index: 1000;
}

::selection {
  background-color: #172533;
}

.middle {
  margin: 30px;
  padding-left: 5px;
  justify-content: center;
  display: flex;
  align-items: center;
}

.circle {
  position: relative;
  margin-left: 10px;
  /* top:0; */
  /* left: 0; */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 0;
  height: 0;
  /* background-color: rgb(7, 77, 107); */
  border: 5px solid #fff;
  border-radius: 50%;
  -webkit-animation: size 2s ease infinite;
          animation: size 2s ease infinite;
}
@-webkit-keyframes size {
  0% {
    width: 0px;
    height: 0px;
  }
  50% {
    width: 25px;
    height: 25px;
  }
  100% {
    width: 0px;
    height: 0px;
  }
}
@keyframes size {
  0% {
    width: 0px;
    height: 0px;
  }
  50% {
    width: 25px;
    height: 25px;
  }
  100% {
    width: 0px;
    height: 0px;
  }
}

@media screen and (max-width: 768px) {
  .circle {
    display: none;
  }
  .middle {
    display: none;
  }
}

.home {
  background-image: url("https://images.unsplash.com/photo-1614851099511-773084f6911d?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow-x: hidden;
  width: 100%;
  scroll-behavior: smooth;
}

.home__main {
  display: flex;
  min-height: 100vh;
}

.home__main1 {
  display: flex;
  flex-direction: column;
  flex: 2 1;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
}

.home__heading {
  margin-top: 20px;
  margin-bottom: 60px;
  color: white;
  font-size: 111px;
  font-family: "Teko", sans-serif;
  text-shadow: rgb(26, 25, 25) 1px 0 5px;
}
.home__button {
  margin-top: 30px;
}

.home__main2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* padding-top: 80px; */
  margin-right: 20px;
  opacity: 0.7;
  flex: 1 1;
}

@media screen and (max-width: 768px) {
  .home__heading {
    font-size: 50px;
    margin-top: -30px;
    /* text-align: center; */
  }

  .home__main {
    display: flex;
    flex-direction: column;
  }

  .home__main1 {
    flex: 1 1;
  }

  .home__main2 {
    margin-right: 0px;
    flex: 3 1;
    display: flex;
    flex-direction: row;

    /* margin-top: -430px; */
  }
}

.output {
  border-radius: 40px;
  padding-top: 60px;
  padding: 20px;
  color: white;
  background: content-box radial-gradient(rgb(43, 40, 59), rgb(3, 40, 53));
}

.output_lines {
  font-size: 22px;
  color: white;
  line-height: 150%;
  -webkit-font-feature-settings: "liga", "clig", "tnum";
          font-feature-settings: "liga", "clig", "tnum";
  font-variant: common-ligatures tabular-nums;
  /* font: italic small-caps bold 16px/2 cursive; */
  font: caption;
  text-align: center;
}

.output__lines > ul > li {
  list-style-type: none;
}

.audio {
  display: none;
  margin: auto;
  margin-bottom: 1rem;
}

.output__output {
  padding: 80px;
  /* opacity: 0.7;
  flex: 1; */
}

@media screen and (max-width: 768px) {
  .output {
    min-height: 100%;
    width: 100%;
    overflow: scroll;
  }

  .output__output {
    padding: 30px;
  }
}

.noSupport {
  color: white;
}

.button__start {
  border-radius: 50%;
  width: 250px;
  height: 250px;
  padding: 15px 30px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 4px;
  text-decoration: none;
  overflow: hidden;
  font-weight: 600;
  margin-left: 30px;
  margin-bottom: 30px;
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-size: 23px;
  transition: 0.2s;
  color: #c46c6c;
  text-shadow: rgb(12, 50, 61) 1px 0 1px;
  background: transparent;
  border-width: 0px;
  border: none;
  outline: none;
}
.button__start:hover {
  box-shadow: 0 0 10px #c46c6c inset, 0 0 40px#c46c6c, 0 0 80px #c46c6c;
  background: #c46c6c;
  color: black;
  text-shadow: rgb(12, 50, 61) 1px 0 1px;
  border-radius: 50%;
}

@media screen and (max-width: 768px) {
  .button__start > span {
    font-size: 90%;
    letter-spacing: 2px;
  }

  .button__start {
    width: 300px;
    height: 50px;
    /* margin-top: -100px; */
    margin-left: 0px;
    margin-right: 0px;
  }

  .button__start:hover {
    border-radius: 20px/50px;
  }
}

video {
  /* opacity: 0.75;
  background-color: rgb(12, 1, 1); */
  object-fit: cover;
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: -1;
  overflow: hidden;
}

.hero-container {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
  overflow: hidden;
}

.hero-container > h1 {
  color: #fff;
  font-size: 100px;
  margin-top: -60px;
  text-shadow: honeydew;
}

@media screen and (max-width: 846px) {
  .hero-container > h1 {
    font-size: 50px;
    /* margin-top: 2rem; */
    text-align: center;
  }
}

.popups {
  margin: 23px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.button {
  font-size: 1em;
  margin: 10px;
  padding: 10px;
  color: #fff;
  border: 2px solid #c46c6c;
  border-radius: 20px/50px;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease-out;
}
.button:hover {
  background: #c46c6c;
  /* color: #292C43; */
  color: black;
  font-weight: bold;
  box-shadow: black 2px 2px 2px;
  -webkit-transform: scale(1.06);
          transform: scale(1.06);
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  /* background: rgba(0, 0, 0, 0.7); */
  /* transition: opacity 500ms; */
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
}
.overlay:target {
  visibility: visible;
  opacity: 0.9;
}

.popup {
  margin: 70px auto;
  padding: 50px;
  background: #232a41;
  border-radius: 5px;
  width: 30%;
  position: relative;
  transition: all 1s ease-in-out;
}

.content {
  max-height: 700px;
  overflow: scroll;
}

.content::-webkit-scrollbar {
  display: none;
}

.popup h2 {
  margin-bottom: 30px;
  color: white;
  font-family: Tahoma, Arial, sans-serif;
}

.popup .close {
  position: absolute;
  top: 20px;
  right: 30px;
  transition: all 200ms;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
  color: #184354;
}
.popup .close:hover {
  color: #c46c6c;
}

.popup .content1 {
  color: white;
  max-height: 30%;
  overflow: hidden;
  font-family: "Andika New Basic", sans-serif;
  font-size: 19px;
}

.popup .content2 {
  color: white;
  height: -webkit-max-content;
  height: max-content;
  overflow: hidden;
  font-family: "Andika New Basic", sans-serif;
  font-size: 16px;
}

.content2 h4 {
  margin-bottom: 11px;
}

.popup .content3 {
  color: white;
  height: -webkit-max-content;
  height: max-content;
  overflow: hidden;
  font-family: "Andika New Basic", sans-serif;
  font-size: 19px;
}

.popup .content4 {
  color: white;
  height: -webkit-max-content;
  height: max-content;
  overflow: hidden;
  font-family: "Andika New Basic", sans-serif;
  font-size: 19px;
}

.content4 > ul > a {
  text-decoration: none;
}

.content4 > ul > a > li {
  text-decoration: none;
  color: white;
}

.content4 > ul > a > li:hover {
  text-decoration: underline;
}

@media screen and (max-width: 768px) {
  .box {
    width: 70%;
  }

  .popup {
    width: 70%;
    padding: 20px;
    margin: 40px auto;
    font-size: 88%;
  }

  /* .content2 > h4 {
    font-size: 88%;
  }

  .content2 > ul > li {
    font-size: 88%;
  }
  .instruction > h2 {
    font-size: 88%;
  }

  .content3 > ul > li {
    font-size: 88%;
  } */

  .popups {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
  }
}

